<template>
  <div class="search-content">
    <!--顶部header-->
    <!-- <div class="search-nav" :style="{top: `${scrollTop}px`}"> -->
    <div class="search-nav">
      <span class="back-icon" @click="navHistoryBack"><i class="icon-back"></i></span>
      <van-search
        v-model="keyword"
        left-icon="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/search.png"
        placeholder="输入商品名、订单号、收货人、手机号"
        input-align="left"
        show-action
        shape="round"
        @input="onInput"
      >
        <div slot="action" @click="onSearch">搜索</div>
      </van-search>
    </div>
    <!-- "!keyword&&!(list.length===0&&keyword)"  -->
    <history-search v-if="!keyword&&!(list.length===0&&keyword)" @doSearch="doSearch"></history-search>
    <div class="search-block" v-if="!(list.length===0||!keyword)">
      <!--上啦加载-->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多啦~"
        @load="onLoad"
        van-clearfix
        :immediateCheck = "false"
      >
        <list :parentData = "list" @onRefreshParent = "onRefresh"></list>
      </van-list>
    </div>
    <no-data v-if="list.length===0&&keyword"></no-data>
    <div class="goods-hot" v-if="list.length===0||!keyword">
      <!-- 猜你喜欢 -->
      <guess-you-lick :class="{'margin-top-header-height':!orderSearchList||orderSearchList.length===0}" :more='true'/>
    </div>
  </div>
</template>

<script>
/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';

/*
 * list列表
*/
import list from '../orderList/list';

/*
 * 历史搜索
*/
import historySearch from './history-search'
import noData from './no-data'

import guessYouLick from '@/components/guessYouLick'
import { createNamespacedHelpers } from 'vuex'
/**
 * vant框架
 */
import Vue from 'vue';
import { List, Toast, Dialog } from 'vant';
const { mapGetters } = createNamespacedHelpers('history')
// 全局注册
Vue.use({ List, Toast, Dialog });

/*
 * 订单搜索列表
*/
export default {
  name: 'orderSearch',
  components: {
    list,
    historySearch,
    guessYouLick,
    noData
  },
  computed: {
    ...mapGetters(['orderSearchList']),
  },
  data() {
    return {
      /**
       * {list} 接口返回数据列表
       * {status} 订单状态 0/null全部，1：待付款，2：待发货，3：待收货，4：待评价，5：售后维权
       * {pageIndex} 页码
       * {pageSize} 每页条数，大小
       * {keyword} 搜索关键字
       * {orderFlag} 0:全部，1:我的，2:用户
       * {error} 加载更多的error信息
       * {loading} 上啦加载更多的loading
       * {finished} 上啦加载完成
       * {scrollTop} 滚动条滚动的位置
       */
      list: [],
      status: '0',
      pageIndex: -1,
      pageSize: 5,
      keyword: '',
      orderFlag: 0,
      error: false,
      loading: false,
      finished: false,
      scrollTop: 0,
    }
  },
  created() {
  },
  beforeMount() {
    this.debouncedBuyAction = this.$util.debounceImmediate(this.onSearch, 600, false)
  },
  mounted() {
    window.addEventListener('scroll', this.handleScroll, true);
  },
  // beforeRouteLeave(to, from, next) {
  //   // 设置下一个路由的 meta
  //   from.meta.keepAlive = true; // 从搜索列表页面跳转到 其他页面，开启搜索列表页面的 keepAlive
  //   console.log('to', to);
  //   console.log('from', from);
  //   console.log('next', next);
  //   next();
  // },
  methods: {

    /**
     * @description 滚动条事件
     */
    handleScroll() {
      // 滚动条距离页面顶部的距离
      // 以下写法原生兼容
      const context = this;
      const _scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      context.scrollTop = _scrollTop
    },

    /**
     * @description header nav 返回上一页
     */
    navHistoryBack () {
      const context = this;
      context.$store.dispatch('doHistoryBack');
    },
    /**
     * @description 实时监听输入（防抖）
    */
    onInput(keyword) {
      this.debouncedBuyAction(keyword)
    },
    /**
     * @description 搜索事件
     */
    onSearch(keyword) {
      const context = this;
      context.pageIndex = 0;
      // context.list = []; // 清理list
      // Toast.loading({
      //   message: '加载中...',
      //   forbidClick: true,
      //   loadingType: 'spinner',
      //   duration: 0
      // });

      // 禁止订单空搜索
      if (!context.keyword) {
        this.$toast('请填写搜索内容')
        return
      }
      /**
       * @description 个人中心 我的订单列表
      */
      context.getList();
      // 添加历史记录
      if (this.keyword) this.$store.commit('history/addOrderHistoryItem', this.keyword.trim())
    },
    doSearch(keyword) {
      const context = this;
      context.pageIndex = 0;
      context.list = []; // 清理list
      if (keyword) context.keyword = keyword.keyword
      // Toast.loading({
      //   message: '加载中...',
      //   forbidClick: true,
      //   loadingType: 'spinner',
      //   duration: 0
      // });
      /**
       * @description 个人中心 我的订单列表
      */

      context.getList();
      // 添加历史记录
      if (this.keyword) this.$store.commit('history/addOrderHistoryItem', this.keyword.trim())
    },
    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      const context = this;
      context.pageIndex = context.pageIndex + 1;
      /**
       * @description 个人中心 我的订单列表
      */
      context.getList();
    },

    /**
     * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
     */
    onRefresh() {
      const context = this;
      context.pageIndex = 0;
      // Toast.loading({
      //   message: '加载中...',
      //   forbidClick: true,
      //   loadingType: 'spinner',
      //   duration: 0
      // });
      /**
       * @description 个人中心 我的订单列表
      */
      context.getList();
    },

    /**
     * @description  个人中心 我的订单列表
     * @param {pageSize} 每页条数大小
     * @param {pageIndex} 第几页
     * @param {status} 订单状态 0/null 全部，1：待付款，2：待发货，3：待收货，4：待评价，5：售后维权
     * @param {keyword} 搜索关键字
     * @param {orderFlag} 0:全部，1:我的，2:用户
     * @param {list} 接口返回数据列表
     * 返回接口请求数据
    */
    getList () {
      const context = this;
      const data = {
        pageSize: context.pageSize,
        pageIndex: context.pageIndex,
        orderStatus: context.status,
        keyword: context.keyword.trim(),
        orderFlag: context.orderFlag,
        chId: 60
      }

      personalApi.getOrderList(data).then(res => {
        const _resData = res.data;
        // console.log(_resData)
        // 清理list
        if (context.pageIndex === 0) {
          context.list = [];
        }
        if (_resData.success) {
          // 手动清除 Toast
          Toast.clear();
          // 判断当前是否是 待收货状态 （isReceived是否收到货 true收到， false未收到）
          _resData.data.orders.map((item, index) => {
            item.orderProducts.map((itemChild, indexChild) => {
              item.isReceived = true; // isReceived是否收到货 true收到， false未收到
              if (itemChild.status === 9 || itemChild.status === 10 || itemChild.status === 11 || itemChild.status === 70) {
                item.isReceived = false;
              }
            })
          });
          // debugger
          context.list = context.list.concat(_resData.data.orders);
          // 加载状态结束
          context.loading = false;
          context.finished = false; // 数据加载完成状态初始化

          // 数据全部加载完成
          if (context.list.length >= _resData.data.countResult || _resData.data.orders.length === 0) {
            context.finished = true;
          }
        } else {
          Toast.fail(_resData.message);
        }
      })
    }

  },
}
</script>

