<template>
  <div class="history" v-if="orderSearchList.length!==0">
    <div class="title">
      <div class="left"><span class="text-icon"></span>最近搜索</div>
      <div class="right">
        <!-- <img @click="deleteHistorySearch" alt :src="`${$cdnImageUrl}/search/delect.png` | imageToHttps" /> -->
        <img @click="deleteHistorySearch" alt :src="deleteIcon" />
      </div>
    </div>
    <ul :class="{'history-content':true,'history-hide':!showMore}" v-if="orderSearchList.length">
      <li class="van-ellipsis" v-for="(item, index) in historySearchListShow" :key="index" @click="doHistorySearch(item)">{{ item }}</li>
      <span class="icon-more" v-if="!showMore && hasMoreBtn" @click="toggleShowMore"></span>
      <span class="icon-more-hide" v-if="showMore && hasMoreBtn" @click="toggleShowMore"></span>
    </ul>
    <div class="nodata" v-else>
      <p>暂无搜索记录</p>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapGetters } = createNamespacedHelpers('history')
export default {
  computed: {
    ...mapGetters(['orderSearchList']),
  },
  data () {
    return {
      historySearchListShow: [],
      showMore: false,
      hasMoreBtn: false,
      deleteIcon: 'data:image/png;base64,'
    }
  },
  created() {
    this.historySearchListShow = this.orderSearchList
    this.showMore = false
    this.hasMoreBtn = false
  },
  mounted() {
    this.toggleHistoryData()
  },
  activated() {
    this.historySearchListShow = this.orderSearchList
    this.showMore = false
    this.hasMoreBtn = false
    this.toggleHistoryData()
  },
  watch: {
    orderSearchList: {
      handler(newVal, oldVal) {
        this.historySearchListShow = this.orderSearchList
        this.toggleHistoryData()
      },
      immediate: true,
      deep: true
    },
  },
  methods: {
    /**
     * @description 历史记录的删除事件
     */
    deleteHistorySearch () {
      if (this.orderSearchList.length === 0) {
        return
      }
      this.$dialog
        .confirm({
          title: '确定清空搜索历史记录吗?',
        })
        .then(() => {
          this.hasMoreBtn = false
          this.$store.commit('history/deleteOrderSearchList')
        })
        .catch(() => {
          // on cancel
        })
    },
    /**
     * @description 历史记录 页面超出两行截取数据
     */
    toggleHistoryData() {
      let idx = 0
      let count = 0
      this.$nextTick(() => {
        let ulChid = document.querySelector('.history-content')
        if (ulChid) {
          ulChid.childNodes.forEach((i, index) => {
            if (i.offsetLeft === 0) {
              count++
              if (count === 3) {
                idx = index - 1
                this.hasMoreBtn = true
              }
            }
          })
          // 超过2行截断数据
          if (idx > 0) {
            this.historySearchListShow = this.orderSearchList.slice(0, idx)
          } else {
            this.historySearchListShow = this.orderSearchList
          }
        }
      })
    },
    toggleShowMore() {
      this.showMore = !this.showMore
      this.toggleHistoryData()
    },
    /**
     * @description 历史记录的点击事件
     */
    doHistorySearch (keyWord) {
      // 触发搜索
      this.$emit('doSearch', {
        keyword: keyWord,
        type: 'history'
      })
    },
  },
}
</script>

